import React, { Component } from 'react';

import { Paper, Button, Typography } from '@material-ui/core';

import { withStyles } from '@material-ui/core/styles';

import RegistrationStepper from './RegistrationStepper';
import GoogleSignInButton from './GoogleSignInButton';

const styles = (theme) => ({
    paper: {
        width: '60%',
        margin: '5% auto',
        height: '40vh'
    },

    emailSignUpButton: {
        textTransform: 'none'
    },

    googleWrapper: {
        margin: '50px auto 0 0',
        display: 'inline-block'
    },

    description: {
        paddingTop: '50px'
    },

    emailSignUpWrapper: {
        marginTop: '40px'
    }
})

class Register extends Component {
    state = {
        showStepper: false
    }

    componentDidMount = () => {
        // clean this listener up on unmount?
        window.gapi.auth2.getAuthInstance().attachClickHandler("g-signin2");
    }

    handleShowStepper = () => {
        this.setState({showStepper: true})
    }

    render(){
        const { classes } = this.props;

        return (
            <React.Fragment>
                {!this.state.showStepper &&
                    <Paper className={classes.paper}>
                        <Typography className={classes.description}>
                            Create an account to visualize data, and access the Simons CMAP API.
                        </Typography>

                        <div className={classes.googleWrapper}>
                            <GoogleSignInButton text='Sign up with Google'/>
                        </div>

                        <div className={classes.emailSignUpWrapper}>
                            <Button className={classes.emailSignUpButton} onClick={this.handleShowStepper}>
                                Sign up with an Email Address
                            </Button>
                        </div>
                    </Paper>                
                }
                {this.state.showStepper && <RegistrationStepper/>}
            </React.Fragment>
        )
    }
}

export default withStyles(styles)(Register);